/**
 * Engram API 
 */

// ==================== LLM  ====================

/**
 * LLM 
 */
export interface LLMPreset {
    /**  */
    id: string;
    /**  */
    name: string;
    /**  connection_profile  */
    source: 'tavern' | 'tavern_profile' | 'custom';
    /**  connection_profile ID source === 'tavern_profile'  */
    tavernProfileId?: string;
    /**  API  source === 'custom'  */
    custom?: CustomAPIConfig;
    /**  */
    parameters: SamplingParameters;
    /**  */
    context: ContextSettings;
    /**  */
    isDefault?: boolean;
    /**  */
    createdAt: number;
    /**  */
    updatedAt: number;
}


/**
 *  API 
 */
export interface CustomAPIConfig {
    /** API  URL */
    apiUrl: string;
    /** API Key */
    apiKey: string;
    /**  */
    model: string;
    /** API / */
    apiSource: APISource;
}

/**
 *  API 
 */
export type APISource = 'openai' | 'anthropic' | 'ollama' | 'vllm' | 'azure' | 'custom';

/**
 * 
 */
export interface SamplingParameters {
    /**  (0-2) */
    temperature: number;
    /** Top-P  (0-1) */
    topP: number;
    /**  tokens */
    maxTokens: number;
    /**  (-2  2) */
    frequencyPenalty: number;
    /**  (-2  2) */
    presencePenalty: number;
}

/**
 * 
 */
export interface ContextSettings {
    /**  (-1 ) */
    maxChatHistory: number;
    /**  */
    includeWorldInfo: boolean;
    /**  token  */
    worldInfoBudget: number;
}

// ====================  ====================

/**
 * 
 */
export interface VectorConfig {
    /**  */
    source: VectorSource;
    /** API  */
    apiUrl?: string;
    /** API Key */
    apiKey?: string;
    /**  */
    model?: string;
    /**  */
    dimensions?: number;
}

/**
 * 
 */
export type VectorSource =
    | 'transformers'  //  transformers
    | 'openai'        // OpenAI Embeddings API
    | 'ollama'        // Ollama
    | 'vllm'          // vLLM
    | 'cohere'        // Cohere
    | 'jina'          // Jina AI
    | 'voyage';       // Voyage AI

// ==================== Rerank  ====================

/**
 * Rerank 
 */
export interface RerankConfig {
    /**  */
    enabled: boolean;
    /** API  */
    url: string;
    /** API Key */
    apiKey: string;
    /**  */
    model: string;
    /**  */
    topN: number;
    /**  (0-1, 0=, 1=Rerank) */
    hybridAlpha: number;
}

// ====================  ====================

/**
 * 
 */
export type PromptCategory =
    | 'text_summary'      // 
    | 'vector_summary'    // 
    | 'trim'              // /
    | 'query_enhance';    // 

/**
 * 
 */
export const PROMPT_CATEGORIES: { value: PromptCategory; label: string; description: string }[] = [
    { value: 'text_summary', label: '', description: '' },
    { value: 'vector_summary', label: '', description: ' JSON/' },
    { value: 'trim', label: '/', description: '' },
    { value: 'query_enhance', label: '', description: '' },
];

/**
 * 
 */
export interface PromptTemplate {
    /**  */
    id: string;
    /**  */
    name: string;
    /**  */
    category: PromptCategory;
    /**  */
    enabled: boolean;
    /**  */
    isBuiltIn: boolean;
    /**  LLM  IDnull  */
    boundPresetId: string | null;
    /**  */
    systemPrompt: string;
    /**  {{chatHistory}}, {{context}}  */
    userPromptTemplate: string;
    /**  */
    outputFormat: 'json' | 'markdown' | 'plain';
    /**  */
    availableVariables: string[];
    /**  */
    createdAt: number;
    /**  */
    updatedAt: number;
}

/**
 * 
 */
export interface PromptTemplateSingleExport {
    version: string;
    exportedAt: number;
    template: Omit<PromptTemplate, 'id' | 'isBuiltIn' | 'enabled' | 'createdAt' | 'updatedAt'>;
}

/**
 * 
 */
export interface PromptTemplateExport {
    version: string;
    exportedAt: number;
    templates: Omit<PromptTemplate, 'id' | 'isBuiltIn' | 'enabled' | 'createdAt' | 'updatedAt'>[];
}

// ====================  ====================

/**
 * 
 */
export interface WorldbookConfig {
    /**  */
    enabled: boolean;
    /** false  */
    includeGlobal: boolean;
}

// ====================  ====================

/**
 * 
 */
export type TrimTriggerType = 'token' | 'floor' | 'count';

/**
 * 
 */
export interface TrimConfig {
    /**  */
    enabled: boolean;
    /**  */
    trigger: TrimTriggerType;
    /** Token trigger='token'  */
    tokenLimit: number;
    /** trigger='floor'  */
    floorLimit: number;
    /** trigger='count'  */
    countLimit: number;
}

// ====================  ====================

/**
 * Engram API 
 */
export interface EngramAPISettings {
    /** LLM  */
    llmPresets: LLMPreset[];
    /**  LLM  ID */
    selectedPresetId: string | null;
    /**  */
    vectorConfig: VectorConfig;
    /** Rerank  */
    rerankConfig: RerankConfig;
    /**  */
    promptTemplates: PromptTemplate[];
    /**  */
    worldbookConfig: WorldbookConfig;
    /**  */
    trimConfig?: TrimConfig;
}

// ====================  ====================

/**
 * 
 */
export const DEFAULT_SAMPLING_PARAMETERS: SamplingParameters = {
    temperature: 0.7,
    topP: 0.95,
    maxTokens: 2048,
    frequencyPenalty: 0,
    presencePenalty: 0,
};

/**
 * 
 */
export const DEFAULT_CONTEXT_SETTINGS: ContextSettings = {
    maxChatHistory: 10,
    includeWorldInfo: true,
    worldInfoBudget: 2048,
};

/**
 * 
 */
export const DEFAULT_VECTOR_CONFIG: VectorConfig = {
    source: 'transformers',
};

/**
 *  Rerank 
 */
export const DEFAULT_RERANK_CONFIG: RerankConfig = {
    enabled: false,
    url: '',
    apiKey: '',
    model: '',
    topN: 5,
    hybridAlpha: 0.5,
};

/**
 *  LLM 
 */
export function createDefaultLLMPreset(name: string = ''): LLMPreset {
    const now = Date.now();
    return {
        id: `preset_${now}`,
        name,
        source: 'tavern',
        parameters: { ...DEFAULT_SAMPLING_PARAMETERS },
        context: { ...DEFAULT_CONTEXT_SETTINGS },
        isDefault: true,
        createdAt: now,
        updatedAt: now,
    };
}

/**
 * 
 */
export function createPromptTemplate(
    name: string,
    category: PromptCategory,
    options: Partial<Omit<PromptTemplate, 'id' | 'name' | 'category' | 'createdAt' | 'updatedAt'>> = {}
): PromptTemplate {
    const now = Date.now();
    return {
        id: `template_${now}_${Math.random().toString(36).slice(2, 8)}`,
        name,
        category,
        enabled: options.enabled ?? false,
        isBuiltIn: options.isBuiltIn ?? false,
        boundPresetId: options.boundPresetId ?? null,
        systemPrompt: options.systemPrompt ?? '',
        userPromptTemplate: options.userPromptTemplate ?? '',
        outputFormat: options.outputFormat ?? 'plain',
        availableVariables: options.availableVariables ?? ['{{chatHistory}}', '{{context}}', '{{char}}', '{{user}}'],
        createdAt: now,
        updatedAt: now,
    };
}

/**
 * 
 */
export function getBuiltInPromptTemplates(): PromptTemplate[] {
    return [
        createPromptTemplate('', 'text_summary', {
            enabled: true,
            isBuiltIn: true,
            systemPrompt: '',
            userPromptTemplate: '\n\n{{chatHistory}}\n\n\n :\n[]',
            outputFormat: 'markdown',
        }),
        createPromptTemplate('', 'vector_summary', {
            enabled: true,
            isBuiltIn: true,
            systemPrompt: '',
            userPromptTemplate: '\n\n{{chatHistory}}\n\n JSON \n- summary: \n- entities:  [{name, type}]\n- relations:  [{subject, predicate, object}]\n- keywords: ',
            outputFormat: 'json',
        }),
        createPromptTemplate('', 'trim', {
            enabled: true,
            isBuiltIn: true,
            systemPrompt: '',
            userPromptTemplate: '\n\n{{context}}\n\n',
            outputFormat: 'markdown',
        }),
        createPromptTemplate('', 'query_enhance', {
            enabled: true,
            isBuiltIn: true,
            systemPrompt: '""""',
            userPromptTemplate: '{{userInput}}\n\n\n{{context}}\n\n',
            outputFormat: 'plain',
        }),
    ];
}

/**
 * 
 */
export const DEFAULT_WORLDBOOK_CONFIG: WorldbookConfig = {
    enabled: true,
    includeGlobal: true,
};

/**
 * 
 */
export const DEFAULT_TRIM_CONFIG: TrimConfig = {
    enabled: false,
    trigger: 'token',
    tokenLimit: 4096,
    floorLimit: 50,
    countLimit: 5,
};

/**
 *  API 
 */
export function getDefaultAPISettings(): EngramAPISettings {
    return {
        llmPresets: [createDefaultLLMPreset()],
        selectedPresetId: null,
        vectorConfig: { ...DEFAULT_VECTOR_CONFIG },
        rerankConfig: { ...DEFAULT_RERANK_CONFIG },
        promptTemplates: getBuiltInPromptTemplates(),
        worldbookConfig: { ...DEFAULT_WORLDBOOK_CONFIG },
    };
}

