/**
 * RegexProcessor - 
 * 
 * 
 */

/**  */
export interface RegexRule {
    /**  ID */
    id: string;
    /**  */
    name: string;
    /**  */
    pattern: string;
    /**  */
    replacement: string;
    /**  */
    enabled: boolean;
    /**  (g, i, m, s) */
    flags: string;
    /**  */
    description?: string;
}

/**  */
export const DEFAULT_REGEX_RULES: RegexRule[] = [
    {
        id: 'remove-think',
        name: '',
        pattern: '<think>[\\s\\S]*?</think>',
        replacement: '',
        enabled: true,
        flags: 'gi',
        description: ' <think>...</think> ',
    },
    {
        id: 'remove-inner-monologue',
        name: '',
        pattern: '<inner_monologue>[\\s\\S]*?</inner_monologue>',
        replacement: '',
        enabled: true,
        flags: 'gi',
        description: ' <inner_monologue>...</inner_monologue> ',
    },
    {
        id: 'remove-system-note',
        name: '',
        pattern: '\\[System:.*?\\]',
        replacement: '',
        enabled: true,
        flags: 'gi',
        description: ' [System:...] ',
    },
    {
        id: 'remove-ooc',
        name: ' OOC ',
        pattern: '\\(OOC:.*?\\)',
        replacement: '',
        enabled: false,
        flags: 'gi',
        description: ' (OOC:...) ',
    },
    {
        id: 'remove-empty-lines',
        name: '',
        pattern: '\\n{3,}',
        replacement: '\n\n',
        enabled: true,
        flags: 'g',
        description: '32',
    },
];

/**
 * 
 */
export class RegexProcessor {
    private rules: RegexRule[] = [];

    constructor(rules?: RegexRule[]) {
        this.rules = rules || [...DEFAULT_REGEX_RULES];
    }

    /**
     * 
     */
    process(text: string): string {
        let result = text;

        for (const rule of this.rules) {
            if (!rule.enabled) continue;

            try {
                const regex = new RegExp(rule.pattern, rule.flags);
                result = result.replace(regex, rule.replacement);
            } catch (e) {
                console.warn(`[RegexProcessor]  "${rule.name}" :`, e);
            }
        }

        return result;
    }

    /**
     * 
     */
    processWithRule(text: string, rule: RegexRule): string {
        try {
            const regex = new RegExp(rule.pattern, rule.flags);
            return text.replace(regex, rule.replacement);
        } catch (e) {
            console.warn(`[RegexProcessor] :`, e);
            return text;
        }
    }

    /**
     * 
     */
    validatePattern(pattern: string, flags: string): { valid: boolean; error?: string } {
        try {
            new RegExp(pattern, flags);
            return { valid: true };
        } catch (e) {
            return {
                valid: false,
                error: e instanceof Error ? e.message : '',
            };
        }
    }

    /**
     * 
     */
    getRules(): RegexRule[] {
        return [...this.rules];
    }

    /**
     * 
     */
    setRules(rules: RegexRule[]): void {
        this.rules = [...rules];
    }

    /**
     * 
     */
    addRule(rule: RegexRule): void {
        this.rules.push(rule);
    }

    /**
     * 
     */
    updateRule(id: string, updates: Partial<RegexRule>): void {
        const index = this.rules.findIndex(r => r.id === id);
        if (index >= 0) {
            this.rules[index] = { ...this.rules[index], ...updates };
        }
    }

    /**
     * 
     */
    deleteRule(id: string): void {
        this.rules = this.rules.filter(r => r.id !== id);
    }

    /**
     * /
     */
    toggleRule(id: string): void {
        const rule = this.rules.find(r => r.id === id);
        if (rule) {
            rule.enabled = !rule.enabled;
        }
    }

    /**
     * 
     */
    resetToDefaults(): void {
        this.rules = [...DEFAULT_REGEX_RULES];
    }

    /**
     * 
     */
    getEnabledCount(): number {
        return this.rules.filter(r => r.enabled).length;
    }
}

/**  */
export const regexProcessor = new RegexProcessor();

export default RegexProcessor;
