/**
 * STContext - SillyTavern 
 * 
 * 
 */

// SillyTavern 
declare global {
    interface Window {
        SillyTavern?: {
            getContext?: () => STContext;
        };
    }
}

/** ST  */
export interface STContext {
    chat: STMessage[];
    characters: STCharacter[];
    name1: string; // 
    name2: string; // 
    characterId: number;
    chatId: string;
}

/** ST  */
export interface STMessage {
    mes: string;
    is_user: boolean;
    is_system?: boolean;
    is_hidden?: boolean;
    name: string;
    send_date?: number;
    extra?: Record<string, unknown>;
}

/** ST  */
export interface STCharacter {
    name: string;
    avatar: string;
    description: string;
}

/**
 *  SillyTavern 
 * @returns ST  null
 */
export function getSTContext(): STContext | null {
    try {
        const ctx = window.SillyTavern?.getContext?.();
        return ctx || null;
    } catch (e) {
        console.warn('[Engram] Failed to get ST context:', e);
        return null;
    }
}

/**
 * 
 */
export function getCurrentChat(): STMessage[] {
    const ctx = getSTContext();
    return ctx?.chat || [];
}

/**
 * 
 */
export function getCurrentCharacter(): { name: string; id: number } | null {
    const ctx = getSTContext();
    if (!ctx) return null;
    return {
        name: ctx.name2,
        id: ctx.characterId,
    };
}



/**
 *  ST 
 */
export function isSTAvailable(): boolean {
    return getSTContext() !== null;
}
